  }
  
  .Spinner_sk-chase-dot__1mb6E {
    width: 100%;
    height: 100%;
    position: absolute;
    left: 0;
    top: 0; 
    animation: Spinner_sk-chase-dot__1mb6E 2.0s infinite ease-in-out both; 
  }
  
  .Spinner_sk-chase-dot__1mb6E:before {
    content: '';
    display: block;
    width: 25%;
    height: 25%;
    background-color: #fff;
    border-radius: 100%;
    animation: Spinner_sk-chase-dot-before__pynyx 2.0s infinite ease-in-out both; 
  }
  
  .Spinner_sk-chase-dot__1mb6E:nth-child(1) { animation-delay: -1.1s; }
  .Spinner_sk-chase-dot__1mb6E:nth-child(2) { animation-delay: -1.0s; }
  .Spinner_sk-chase-dot__1mb6E:nth-child(3) { animation-delay: -0.9s; }
  .Spinner_sk-chase-dot__1mb6E:nth-child(4) { animation-delay: -0.8s; }
  .Spinner_sk-chase-dot__1mb6E:nth-child(5) { animation-delay: -0.7s; }
  .Spinner_sk-chase-dot__1mb6E:nth-child(6) { animation-delay: -0.6s; }
  .Spinner_sk-chase-dot__1mb6E:nth-child(1):before { animation-delay: -1.1s; }
  .Spinner_sk-chase-dot__1mb6E:nth-child(2):before { animation-delay: -1.0s; }
  .Spinner_sk-chase-dot__1mb6E:nth-child(3):before { animation-delay: -0.9s; }
  .Spinner_sk-chase-dot__1mb6E:nth-child(4):before { animation-delay: -0.8s; }
  .Spinner_sk-chase-dot__1mb6E:nth-child(5):before { animation-delay: -0.7s; }
  .Spinner_sk-chase-dot__1mb6E:nth-child(6):before { animation-delay: -0.6s; }
  
  @keyframes Spinner_sk-chase__h4d3d {
    100% { transform: rotate(360deg); } 
  }
  
  @keyframes Spinner_sk-chase-dot__1mb6E {
    80%, 100% { transform: rotate(360deg); } 
  }
  
  @keyframes Spinner_sk-chase-dot-before__pynyx {
    50% {
      transform: scale(0.4); 
    } 100%, 0% {
      transform: scale(1.0); 
    } 
  }
